body {
  background-color: rgb(35 39 47);
  /* Grabbed from the React tutorial page */
  font-family: Helvetica;
  opacity: 0.9;
}

h1,
h2,
h3,
h4 {
  font-family: Helvetica;
  color: rgb(35 39 47);
}

hr {
  border-color: rgb(35 39 47);
}

:link {
  text-decoration: none;
  color: #4d5aa0;
}

/* for unvisited links */
:visited {
  text-decoration: none;
  color: #644451;
}

/* for visited links */
a:hover {
  text-decoration: underline;
  color: blue;
}

/* when mouse is over link */
code {
  font-size: 1.2em;
}

ul {
  padding-inline-start: 20px;
}

.bigdiv {
  background-color: rgb(88 196 220);
  color: rgb(35 39 47);
  /*Color grabbed from the React tutorial page*/
  /*#19181a;*/
  width: 800px;
  margin: auto;
  padding: 18px;
  /*padding-top:0; */
  opacity: 1.0;
  /*border:2px solid #BADA55*/
  /* bad ass border, for debugging */
  overflow: hidden;
}

.jsapp {
  display: flex;
  justify-content: center;
  width: auto;
}

.checklist {
  list-style: none;
  /* Remove default numbering */
  counter-reset: item;
  /* Initialize counter */
  padding: 0;
  /* Remove default padding */
}

.checklist li {
  position: relative;
  /* Position relative to allow absolute positioning of the checkmark */
  counter-increment: item;
  /* Increment the counter for each list item */
  margin: 20px 0;
  /* Add space between list items */
  padding-left: 20px;
  /* Add padding to make room for the number */
}

.checklist li::before {
  content: counter(item);
  /* Show the number */
  position: absolute;
  /* Position the number absolutely */
  left: 0;
  /* Align to the left */
  top: 0;
  /* Align to the top */
  font-weight: bold;
  /* Make the number bold */
}

.checklist li::after {
  content: "✔️"; /* Show the checkmark */
  position: absolute; /* Position the checkmark absolutely */
  left: 0; /* Align to the left */
  top: 0; /* Align to the top */
  color: green; /* Change the color of the checkmark */
  font-size: 1.2em; /* Adjust the size of the checkmark */
  transform: translate(-50%, -50%); /* Center the checkmark over the number */
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}